.app {
  height: 100%;
  width: 100%;
  position: relative;
}

.tile {
  transform: scale(1);
  height: 100%;
  width: 100%;
}

.tile-background {
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  z-index: -1;
}

.tile-scene {
  display: flex;
  align-items: center;
  justify-content: center;
  perspective: 400px;
}
