.moon {
  margin-right: 3px;
}

.sun {
  margin-left: 3px;
}

.tile-toggle {
  align-items: center;
  background-color: gray;
  border-radius: 30px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  position: absolute;
  right: 20px;
  top: 20px;
  width: 55px;
  z-index: 1000;
}

.current-toggle {
  background-color: white;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.8);
  border-radius: 50%;
  height: 20px;
  position: absolute;
  width: 20px;
}

.toggle-left {
  transform: translateX(-14px);
  transition: transform 0.5s;
}

.toggle-right {
  transform: translateX(14px);
  transition: transform 0.5s;
}

